.banner4-main-div {
    position: relative;
    display: flex;
}

.climed-man-image {
    width: 60%;
    padding-top: 1%;

    img {
        width: 100%;
        height: 400px;
    }
}

.banner4-box {
    position: relative;
    width: 40%;
    height: 432px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 5%;
    background-color: #1B252C;

    h4 {
        text-align: left;
        font: normal normal 600 48px/60px Source Serif Pro;
        letter-spacing: 0px;
        color: #FFFFFF;
        opacity: 1;
    }

    p {
        text-align: left;
        font: normal normal 600 20px/25px Muli;
        letter-spacing: 0px;
        color: #FFFFFF;
        opacity: 1;
    }

    button {
        width: 180px;
        height: 40px;
        border: 2px solid #FFFFFF;
        background: none;
        text-align: center;
        font: normal normal bold 14px/16px Muli;
        letter-spacing: 0px;
        color: #FFFFFF;
        text-transform: uppercase;
        border-radius: 10px;
        opacity: 1;
    }
}

@media (max-width : 786px) {
    .climed-man-image {
        width: 100%;
    }

    .banner4-box {
        display: none;
    }

    .banner4-main-div {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    .banner4-mobile-box{
        display: flex;
    }
}

@media (min-width : 786px) {
    .banner4-mobile-box {
        display: none;
    }
}

.banner4-mobile-box {
    transform: translateY(-60px);
    width: 80%;
    height: 310px;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background-color: #1B252C;

    h4 {
        text-align: center;
        font: normal normal 600 36px/45px Source Serif Pro;
        letter-spacing: 0px;
        color: #FFFFFF;
        opacity: 1;
    }

    p {
        text-align: center;
        font: normal normal 600 20px/24px Muli;
        letter-spacing: 0.6px;
        color: #FFFFFF;
        opacity: 1;
    }

    button {
        width: 180px;
        height: 40px;
        border: 2px solid #FFFFFF;
        border-radius: 10px;
        background: none;
        text-align: center;
        font: normal normal bold 14px/16px Muli;
        letter-spacing: 0px;
        color: #FFFFFF;
        text-transform: uppercase;
        opacity: 1;
    }
}
.mapiconWrapper {
    position: absolute;
    left: 0%;
    padding-left: 58%;
    bottom: 10%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
   margin-left: -1em;

    .mapPinImg {
        width: 33px;
        height: 40px;
        margin-left: 1.6rem;

    }

    .mapMaxHeaderImg {
        width: 102px;
    height: 8px;
    margin-left: -1em;

    }
  }
@media (max-width : 786px) {
    .banner4-main-div{
        img{
            width: 100%;
        }
    }
    .mapiconWrapper{
        padding-left: 22%;
    transform: translateY(-273px);
    }
}
